<script>
import PropTypes from "../editor/propTypes";

export default {
  name: "Editor",
  data() {
    return {
      data: [],
      focused: null
    };
  },
  created() {
    const { page } = this;
    const { token } = this.$auth;

    // Call api function to get page information
    this.$api(`/edit/${page}`, { body: { token } })
      .then(data => {
        this.data = data;
      }).catch(console.error);
  },
  computed: {
    page() {
      return this.$route.params.page;
    }
  },
  methods: {
    openSidebar() {
      this.sidebarIsOpen = !this.sidebarIsOpen;
    },
    addComponent(e) {
      const {target} = e;

      if(target.matches("a.item")) {
        const name = target.textContent;
        alert(`Added a '${name}' component. Poof!`);
      }
    }
  }
};
</script>